.head-container {
  display: flex;
  flex-direction: column;
  align-items: center;
  margin-top: 60px;
}
.head-image {
  width: auto;
  transition: max-width 1s, min-width 1s;
}
.head-image-expanded {
  min-width: 290px;
  max-width: 270px;
}
.head-image-contracted {
  min-width: 90px;
  max-width: 80px;
}
.head-text {
  font-family: "Hachi Maru Pop", cursive;
  margin: 5px 0 15px 0;
  transition: font-size 1s;
}
.head-text-expanded {
  font-size: 36px;
}
.head-text-contracted {
  font-size: 13px;
}
